
import house from '../lib/house.js';
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'


export default {
components: {Group, XAddress},
data() {
return {
name: '',
addressData: ChinaAddressV4Data,
showAddress: false,
condtion_address: [],
houses: []
}
},
mounted() {
this.loadData();
},
methods: {
toDetail(houseId) {
this.$router.push({
name: 'detail',
query: {
id: houseId
}
})
},
loadData() {
var rs = [];
this.$vux.loading.show({
text: '加载中....'
});
house.getHouses((data) => {
this.$vux.loading.hide()
if (data && data.houses) {
data.houses.forEach(h => {
if (this.canSee(h)) {
rs.push(h);
}
});
}
});
this.houses = rs;
},
canSee(house) {
if (house.rented) {
return false;
}
return true;
},
typeName(type) {
var name = null;
this.rentHouseTypes.forEach(rh => {
if (rh.key == type) {
name = rh.value;
return false;
}
});
return name;
},
getAddressNames(address) {
var addressNames = [];
if (address && address.length > 0) {
var map = {};
var length = address.length;
address.forEach(ad => {
map[ad] = true;
});
var nameMap = {};
ChinaAddressV4Data.forEach(cv => {
if (length == 0) {
return false;
}
if (map[cv.value]) {
nameMap[cv.value] = cv.name;
delete map[cv.value];
length--;
}
});

address.forEach(ad => {
var name = nameMap[ad];
if (name) {
addressNames.push(name);
}
});
}
return addressNames.join(' ');
},
getTimeStr(str) {
return str ? moment(str).format('YYYY-MM-DD hh:mm') : '未知'
},
filter(address) {
if (this.condtion_address.length == 0) {
return true;
}

var flag = true;
this.condtion_address.forEach((ar, i) => {
if (address.length >= i - 1) {
if (address[i] != ar) {
flag = false;
return false;
}
}
});
return flag;
},
refreshHouse(){
this.loadData();
}
},
computed: {
rentHouseTypes() {
return this.$store.state.rentHouseTypes;
}
}
}



